(function() {
  'use strict';

  angular.module('app', [
    'pascalprecht.translate',
    'tmh.dynamicLocale',
    'ui.router',
  ]);

})();

(function() {
    'use strict';

    angular.module('app').component('home', {
        controller: HomeController,
        controllerAs: 'vm',
        templateUrl: 'app/home/home.view.html',
    });

    /** @ngInject */
    function HomeController($log, $translate, SAMPLE_CONSTANT) {
        var vm = this;
        
        // Make functions accessible from template
        vm.updateValue = updateValue;
        vm.addToList = addToList;
        vm.moveList = moveList;
        vm.addList = addList;
        
        vm.lists = [
        {
            name: 'Bob',
            items: ["Make Lunch", "Tie Shoes", "Count By Twos"],
            tempTask: 'add me!'
        }, {
            name: 'Andy',
            items: [],
            tempTask: ''

        }, {
            name: 'Sally',
            items: [],
            tempTask: ''

        }, {
            name: 'Rachel',
            items: [],
            tempTask: ''

        } ];
        
        vm.listTemplate = [{
            name: '',
            items: [],
            tempTask: ''
        }]
        
        function updateValue(index) {
            // Required for 2 way bindings.
        }
        
        function addToList(index) {
            var target = vm.lists[index].items;
            var toDo = vm.lists[index].tempTask;
            
            if ( toDo.length > 0 ) {
                target.push(toDo);
                vm.lists[index].tempTask = '';
            } else {
                alert('Please Enter A Task ðŸ™Œ')
            }
            
            console.log(vm);
        }
        
        
        function moveList(itemIndex, listIndex, direction) {
            
            var target = vm.lists[listIndex].items[itemIndex],
                moveby = 0,
                listTotal = vm.lists.length,
                destination = null,
                destinationIndex = null;

            // find direction to move
            switch (direction) {
                case 'L':
                    moveby -= 1;
                    break
                case 'R':
                    moveby += 1;
                    break
            }
            
            console.log(itemIndex);
            
            // New Array index
            destinationIndex = _findListNumber(moveby, listIndex, listTotal);
            
            destination = vm.lists[destinationIndex].items;
            
            //Move to Proper place
            destination.push(target);
                        
            //Remove Source
            vm.lists[listIndex].items.splice(itemIndex, 1);
            
            // Wrap around logic
            function _findListNumber(m, l , t) {
                return ( ( l + m + t ) % t ); // avoid negitive integers, add total 
            }
                        
        }
        
        function addList(name) {
            vm.lists.push(vm.listTemplate);
            vm.lists[vm.lists.length - 1].name = 'George'
        }

    }

})();
(function() {
  'use strict';

  angular.module('app').run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, tmhDynamicLocale) {
    $rootScope.$on('$translateChangeSuccess', function(event, data) {
      tmhDynamicLocale.set(data.language);
      document.documentElement.setAttribute('lang', data.language);
    });

    $log.debug('App run block end');
  }

})();

(function() {
  'use strict';

  angular.module('app').config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider.state('home', {
      url: '/',
      component: 'home',
    });

    $urlRouterProvider.otherwise('/');
  }

})();

(function() {
  'use strict';

  angular.module('app').
      constant('SAMPLE_CONSTANT', 'Sample constant');

})();

(function() {
  'use strict';

  angular.module('app').config(configBlock);

  /** @ngInject */
  function configBlock($locationProvider, $logProvider, $translateProvider,
                       tmhDynamicLocaleProvider) {
    $locationProvider.
        html5Mode(true);

    $logProvider.
        debugEnabled(true);

    $translateProvider.
        useStaticFilesLoader({
          prefix: '/locales/',
          suffix: '.json',
        }).
        preferredLanguage('en').
        fallbackLanguage('en').
        useSanitizeValueStrategy('escape').
        useMissingTranslationHandlerLog();

    tmhDynamicLocaleProvider.
        localeLocationPattern('/locales/angular-locale_{{locale}}.js').
        defaultLocale('en');
  }

})();

angular.module('app').run(['$templateCache', function($templateCache) {$templateCache.put('app/home/home.view.html','<div class=homeContainer><div class=container><div class=row><div class="listContainer col-md-3" ng-repeat="list in vm.lists track by $index"><div class=well><h2>{{ list.name }}</h2><ul class=list><li ng-repeat="item in list.items track by $index"><button class="btn btn-default pull-left" ng-click="vm.moveList($index, $parent.$index, \'L\')">\uD83D\uDC48</button> <span class=textDesc>{{item}}</span> <button class="btn btn-default pull-right" ng-click="vm.moveList($index, $parent.$index, \'R\')">\uD83D\uDC49</button></li></ul><div class="addNew form-group"><input type=text ng-model=vm.lists[$index].tempTask ng-change=vm.updateValue($index) ng-trim=true placeholder="Add a task"> <button class="btn btn-default" ng-click=vm.addToList($index)>+</button></div></div></div><div class=col-md-3><button class="btn btn-default" ng-click=vm.addList()>+</button></div></div></div></div>');}]);